<template>
  <div id="log"
       style="height: 100vh;background-color: #001529;display: flex;align-items: center;justify-content: right ;">
    <div style="display:flex;background:transparent;width:25% ">
      <!--      <div style="flex:1">
              <img src="@/assets/login.png" style="width:100%"/>
            </div>-->
      <div style="flex:1 ;display: flex;align-items: center;justify-content: left">
        <el-form ref="registerRef" :model="user" :rules="rules" style="width: 70%">
          <div style="font-size: 20px;color: #9fe7ff;text-align: left;font-weight: bold;margin-bottom: 20px">用户注册</div>
          <el-form-item prop="username">
            <el-input v-model="user.username" placeholder="请输入用户名" prefix-icon="el-icon-user"></el-input>
          </el-form-item>
          <el-form-item prop="name">
            <el-input v-model="user.name" placeholder="请输入您的真实姓名" prefix-icon="el-icon-s-custom"></el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input v-model="user.password" placeholder="请输入密码" prefix-icon="el-icon-lock" show-password></el-input>
          </el-form-item>
          <el-form-item prop="confirmPassword">
            <el-input v-model="user.confirmPassword" placeholder="请再次输入密码" prefix-icon="el-icon-lock"
                      show-password></el-input>
          </el-form-item>
          <el-form-item>
            <el-button style="width: 100%" type="primary" @click="register">注册</el-button>
          </el-form-item>
          <div style="display: flex;height: 20px">
            <div style="flex: 1;color:#b5f1ff;display: flex;justify-content: left">已有账号,请
              <span style="cursor: pointer;color: #0f9876" @click="$router.push('/login')">登录</span>
            </div>

          </div>
        </el-form>

      </div>

    </div>
  </div>

</template>
<script>

import axios from "axios";
import config from "@/config/config";
import request from "@/utils/request";

export default {
  name: 'register',
  data: function () {
    return {
      user: {
        username: '',
        password: '',
        name: '',
        confirmPassword: ''
      },
      rules: {
        username: [
          {required: true, message: '请输入账号', trigger: 'blur'},
          {min: 4, max: 10, message: '账号长度在4到10之间', trigger: 'blur'}
        ], password: [
          {required: true, message: '请输入密码', trigger: 'blur'},
          {min: 6, max: 10, message: '姓名长度在6到10之间', trigger: 'blur'}
        ], name: [
          {required: true, message: '请输入姓名', trigger: 'blur'},
          {min: 2, max: 20, message: '姓名长度在2到20之间', trigger: 'blur'}
        ], confirmPassword: [
          {required: true, message: '请再次输入密码', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    register: function () {
      this.$refs['registerRef'].validate(validate => {
        if (validate) {
          if (this.user.password === this.user.confirmPassword) {

            request.post("/user/register", this.user).then(res => {
              if (res.data.code === 1) {
                this.$router.push('/login')
                this.$message.success("注册成功")
              } else {
                this.$message.error(res.data.message)
              }
            })
          } else {
            this.$message.error("两次密码输入不一致")
          }

        }


      })


    }
  }
}
</script>
<style scoped>
#log {

  background: url("../assets/375d441b0a9f6ff.jpg");
  background-size: 120vw;


}
</style>